.summerAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#f7d53b), to(#edd566));
    background: -webkit-linear-gradient(right, #f7d53b, #edd566);
    background: -o-linear-gradient(right, #f7d53b, #edd566);
    background: linear-gradient(270deg, #f7d53b, #edd566);
    -webkit-transition:  1s ease all;
    -o-transition:  1s ease all;
    transition:  1s ease all;
}

.winterAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#a4d0e8), to(#8faebe));
    background: -webkit-linear-gradient(right, #a4d0e8, #8faebe);
    background: -o-linear-gradient(right, #a4d0e8, #8faebe);
    background: linear-gradient(270deg, #a4d0e8, #8faebe);
    -webkit-transition:  1s ease all;
    -o-transition:  1s ease all;
    transition:  1s ease all;
}
.hotSummerAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#d74240), to(#a12b29));
    background: -webkit-linear-gradient(right, #d74240, #a12b29);
    background: -o-linear-gradient(right, #d74240, #a12b29);
    background: linear-gradient(270deg, #d74240, #a12b29);
    -webkit-transition:  1s ease all;
    -o-transition:  1s ease all;
    transition:  1s ease all;
}
.foggyAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#707579), to(#585858));
    background: -webkit-linear-gradient(right, #707579, #585858);
    background: -o-linear-gradient(right, #707579, #585858);
    background: linear-gradient(270deg, #707579, #585858);
    -webkit-transition:  1s ease all;
    -o-transition:  1s ease all;
    transition:  1s ease all;
}
.rainyAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#39485b), to(#282e36));
    background: -webkit-linear-gradient(right, #39485b, #282e36);
    background: -o-linear-gradient(right, #39485b, #282e36);
    background: linear-gradient(270deg, #39485b, #282e36);
    -webkit-transition:  1s ease all;
    -o-transition:  1s ease all;
    transition:  1s ease all;
}



.bgAnimation{
    width: 100%;
    height: 100%;
    background-size: 400% 400%;
    -webkit-animation: AnimationName 5s ease infinite;
    animation: AnimationName 5s ease infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


